          flex-direction: column;
  margin-top: 30px;
}











  .user-input {
    border: 1px solid;
    border-color: yellowgreen;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
