.user-container {
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 15px;
    border: 1px solid;
    border-radius: 10px;
    background-color: #ffffff;
    margin: 30px 0 15px 0;
}

.user-avatar {
  min-width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
    width: 50px;
    height: 50px;
