
body {
  background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: fit-content;
 
}

.flex h1 {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}


.header {
  width: 100%;
  height: 60px;
  font-size: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
}

.header ul {    
  list-style-type: none;
}

.header li {
  position: relative;
  margin: 5px 15px;
  
}

.header a {
  text-decoration: none;
  text-transform: uppercase;
  color: black;
}

.home-page {
  display:flex;
  flex-direction: column;
  margin-top: 30px;
}








